@import "mixins";

body {
	min-width: 320px;
	margin: 0 auto;
	color: #000;
	font-family: 'Skoda' !important; }

.skoda_header_dropdown,
.mob_button,
.model_capt_hide {
	display: none; }

// .dropdown-toggle:hover,
.dropdown-toggle:active,
.dropdown-toggle:focus {
	text-decoration: none !important;
	font-family: 'Skoda-Bold';
	// font-weight: bold
	color: $main-green !important; }

.dropdown_menu {
	display: inline-block;
	padding: 0; }
.dropdown_li {
	display: inline-block;
 }	// vertical-align: middle

.skoda_header {
	background: $white;
	height: 70px;
	width: 100%;
	// padding: 13px 30px
	position: relative;
	// padding-left: 30px
	.skoda_header_nav {
		a:hover {
			text-decoration: none;
			color: $main-green; }
		a {
			color: #000;
			font-size: 16px;
			// vertical-align: middle
			text-align: center; } }
	.skoda_logo {
		display: inline-block;
		text-align: center;
		margin-left: 30px;
		padding-top: 13px;
		padding-bottom: 13px;
 }		// vertical-align: middle
	.skoda_header_nav {
		list-style-type: none;
		display: inline-block;
		padding: 0;
		margin: 0;
		// vertical-align: middle
		// .skoda_header_nav_list-item:nth-child(6)
		/// display: none
		.skoda_header_nav_list-item {
			display: inline-block;
			// vertical-align: middle
			.skoda_header_nav_hide_list {
				display: none;
				padding: 10px 15px;
				list-style-type: none;
				border-bottom: 1px solid $main-grey;
				.skoda_header_nav_hide_li {
					display: block;
					padding-left: 55px;
					a:hover {
						color: darken($main-green, 10%); }
					a {
						text-transform: uppercase;
						font-size: 14px;
						padding-top: 15px;
						padding-bottom: 15px;
						color: $main-green;
						font-family: 'Skoda-Bold';
						display: block; } } }

			a {
				padding: 26px 15px; } } }

	.skoda_models_dropdown_menu {
		box-shadow: none;
		border: none;
		border-radius: 0;
		width: 96%;
		left: 2%;
		margin: 0;
		padding: 30px 0%;
		// text-align: center
		.skoda_models_dropdown_menu_container {
			font-size: 0;
			letter-spacing: -1px; }
		.skoda_models_dropdown_menu_h {
			font-size: 36px;
			letter-spacing: normal;
			text-align: center; }
		.skoda_models_dropdown_menu_li {
			display: inline-block;
			width: 24%;
			border-bottom: 1px solid $main-grey;
			.skoda_models_dropdown_menu_link:hover {
				background: none;
				color: #000;
				.skoda_models_dropdown_menu_img {
					@include transform(scale(1.05)); } }
			.skoda_models_dropdown_menu_link {
				display: block;
				padding-left: 0;
				padding-right: 0; }
			.skoda_models_dropdown_menu_img,
			.skoda_models_dropdown_menu_caption {
				display: block;
				text-align: center; }
			.skoda_models_dropdown_menu_img {
				display: inline-block;
				width: 90%;
				@include transition-ease(1s); }
			.skoda_models_dropdown_menu_caption {
				letter-spacing: normal;
				font-size: 20px;
				font-family: 'Skoda-Bold';
				text-transform: uppercase; } } }


	.header_right_dlock {
		display: inline-block;
		font-size: 0;
		letter-spacing: -1px; }

	.skoda_header_account:hover {
		cursor: pointer; }
	.skoda_header_account {
		border-left: 1px solid #ddd;
		.skoda_header_account_button {
			padding: 14px	20px;
			font-size: 30px;
			color: $main-green; }
		.skoda_header_account_dropdown {
			border-radius: 0;
			box-shadow: none;
			border: none;
			border-bottom: 1px solid $main-grey;
			padding: 0;
			margin: 0;
			width: 300px;
			letter-spacing: 0;
			.account_photo {
				display: inline-block;
				vertical-align: top; }
			.account_info {
				display: inline-block;
				.account_pass_change {
					.account_pass_change_link {
						display: inline-block;
						color: $main-green;
						font-size: 14px;
						padding: 10px 0; } }
				.account_name {
					font-family: 'Skoda-Bold';
					font-size: 16px; } }
			.account_view_profile_button:hover {
				background: darken($main-green, 10%); }
			.account_view_profile_button {
				@include transition(0.5s);
				margin-top: 20px;
				padding: 10px 0;
				display: block;
				font-family: 'Skoda-Bold';
				color: $white;
				background: $main-green;
				text-align: center; }
			.account_footer_button {
				font-size: 0;
				letter-spacing: -1px; }
			.account_pass_change_button {
				border-right: 1px solid $main-grey; }

			.account_pass_change_button,
			.account_out_button {
				display: inline-block;
				width: 50%;
				font-size: 14px;
				letter-spacing: normal;
				color: $main-green;
				padding-top: 10px;
				padding-bottom: 10px;
				text-align: center; } } }



