			text-decoration: none !important;
			font-family: 'Skoda-Bold';
			font-weight: bold;
			color: $main-green; }
		a {
			font-size: 16px; }
		.skoda_header_tools_dropdown:hover {
			color: $main-green;
			text-decoration: none; }
		.skoda_header_tools_dropdown {
			font-size: 14px;
			color: #000;
			letter-spacing: 0;
			padding: 25px 15px 25px 55px;
