	.skoda_header_link {
		display: inline-block;
		// position: absolute
		// top: 0
		// right: 190px
		.header_right_dlock {
			font-size: 14px;
			letter-spacing: 0;
			a:hover {
				text-decoration: none;
				color: $main-green; }

			a {
				padding: 25px 15px 25px 55px;
				display: block;
				color: #000; } } }
	.skoda_header_location {
		// background: url('https://github.com/westwebdev/westwebdev.github.io/blob/master/skoda/img/header_location.bin') no-repeat 15px 50%
		background: url('/img/header_location.bin') no-repeat 15px 50%;
		background-size: 30px 30px; }
	.skoda_header_helm {
		background: url('/img/header_helm.bin') no-repeat 15px 50%;
		// background: url('https://github.com/westwebdev/westwebdev.github.io/blob/master/skoda/img/header_helm.bin') no-repeat 15px 50%
		background-size: 30px 30px; }
	.skoda_header_book {
		// background: url('https://github.com/westwebdev/westwebdev.github.io/blob/master/skoda/img/header_book.bin') no-repeat 15px 52%
		background: url('/img/header_book.bin') no-repeat 15px 52%;
		background-size: 30px 30px; }
	.skoda_header_check {
		background: url('/img/header_check.bin') no-repeat 15px 52%;
		background-size: 30px 30px; } }



.skoda_jumbotron_section {
	position: relative;
	.skoda_jumbotron_link {
		width: 100%;
		height: 500px;
		display: block;
		.skoda_jumbotron_link_pic_1 {
			position: relative;
			background: url("/img/skoda_banner.png") no-repeat 50% 0%;
			// background: url("https://raw.githubusercontent.com/westwebdev/westwebdev.github.io/master/skoda/img/skoda_banner.png") no-repeat 50% 0%
			// background-size: 100%
			background-size: 1920px 500px;
			height: 500px; }
		.skoda_jumbotron_link_pic_2 {
			position: absolute;
			top: 0;
			left: 0;
			background: url("/img/skoda_gradient.png") no-repeat 100% 0%;
			// background: url("https://raw.githubusercontent.com/westwebdev/westwebdev.github.io/master/skoda/img/skoda_gradient.png") no-repeat 100% 0%
			height: 500px;
			width: 100%; } }
	.skoda_jumbotron_button {
		position: absolute;
		padding-left: 40px;
		left: 0;
		bottom: 40px;
		font-family: 'Skoda-Bold';
		.skoda_jumbotron_h {
			color: $white;
			font-family: 'Skoda-XBold';

			// font-weight: 900
			text-transform: uppercase;
			font-size: 48px;
			margin-bottom: 20px; }
		.skoda_jumbotron_btn:hover {
			text-decoration: none;
			background: darken($main-green, 10%); }
		.skoda_jumbotron_btn {
			background: $main-green;
			color: $white;
			font-weight: bold;
			border-radius: 3px;
			padding: 15px 40px; } } }

.skoda_text_section {
	padding: 30px 0;
	color: #000;
	p {
		margin-bottom: 10px; }
	.skoda_text_section_container {
		max-width: 700px; }
	.skoda_text_section_h {
		margin-top: 0;
		font-family: 'Skoda-Bold';
		font-size: 30px;
 }		// font-weight: 800
	.skoda_text_section_name {
		font-family: 'Skoda-Bold';
 }		// font-weight: bold
	.skoda_text_section_mail {
		a:hover {
			color: darken($main-green, 20%); }
		a {
			color: $main-green; } } }

.skoda_tools_section {
 .skoda_tools_section_container {}
	font-size: 0;
	letter-spacing: -1px;
	text-align: center;
	.skoda_tools_item:hover {
		text-decoration: none; }
	.skoda_tools_item {
		max-width: 225px;
		width: 33.333%;
		box-sizing: border-box;
		font-size: 0;
		letter-spacing: -1px;
		position: relative;
		color: #000;
		display: inline-block;
		// height: 300px
		// padding: 0 2%
		vertical-align: top;
		div {
			font-size: 14px;
			letter-spacing: 0; }
		.skoda_tools_item_img {
			margin-top: 60px;
			margin-bottom: 20px; }
		.skoda_tools_item_h {
			font-size: 14px;
			font-family: 'Skoda-Bold';
			margin-bottom: 10px;
			text-transform: uppercase;
			letter-spacing: normal; }
		.skoda_tools_item_text {
			line-height: 24px;
			height: 50px;
			letter-spacing: normal; }

		.skoda_tools_item_link:hover {
			text-decoration: underline; }
		.skoda_tools_item_link {
			margin-top: 30px;
			margin-bottom: 60px;
			font-family: 'Skoda-Bold';
			// font-weight: bold
			color: $main-green; } } }

.item_link_hover:hover .item_link_hover_dark {
	opacity: .5; }
.hover-overshadow {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
	display: none;
	background: white;
	z-index: 3;
	opacity: 0.5; }

.bottom_menu_column_new {
	text-align: center;
	display: none;
	.bottom_menu_column_new_h {
		display: inline-block;
		color: $main-green;
		font-family: 'Skoda-Bold';
		font-weight: bold;
		font-size: 14px; }
	.bottom_menu_column_new_ul {
		list-style-type: none;
		padding: 0; }
	.bottom_menu_column_new_li {
		padding: 3px 0;
		a:hover {
			text-decoration: none;
			color: #000; }
		a {
			display: inline-block;
			color: #000; } }

	.bottom_menu_column_new_li_call {
		i {
			font-size: 20px;
			margin-right: 10px; }
		a:hover {
			color: #fff;
			background: darken($main-green, 12%); }
		a {
			background: $main-green;
			padding: 10px 20px;
			border-radius: 3px;
			font-weight: bold;
			margin-bottom: 5px;
			color: #fff;
			font-family: 'Skoda-Bold';
			font-size: 14px; } }
	.bottom_menu_column_new_li_mail {
		display: inline-block;
		a {
			color: $main-green;
			font-weight: bold;
			font-size: 12px; } } }


.bottom_menu {
	background: $main-grey;
	padding-top: 15px;
	padding-bottom: 15px;
	.bottom_menu_container {
		max-width: 1170px; }
	.bottom_menu_dropdown_button {
		width: 100%;
		display: none; }
	.bottom_menu_dropdown {
		padding: 0;
		position: relative; }
	.bottom_menu_column {
		display: inline-block;
		vertical-align: top;
		padding-left: 2%;
		padding-right: 2%;
		font-size: 0;
		letter-spacing: -1px;
		.bottom_menu_column_h {
			font-family: 'Skoda-Bold';
			// font-weight: bold
			font-size: 14px;
			letter-spacing: 0; }
		.bottom_menu_column_ul {
			font-size: 14px;
			letter-spacing: 0;
			list-style-type: none;
			padding: 0;
			.bottom_menu_column_li {
				padding: 2px 0;
				a {
					color: #000; } }
			.bottom_menu_column_li_call:hover {
				background: darken($main-green, 12%);
				a {
					color: #fff;
					text-decoration: none; } }
			.bottom_menu_column_li_call {
				background: $main-green;
				border-radius: 3px;
				font-weight: bold;
				margin-bottom: 5px;
				i {
					font-size: 20px;
					margin-right: 10px; }
				a {
					color: #fff;
					font-family: 'Skoda-Bold';
					font-size: 14px;
					display: block;
					padding: 10px 20px; } }
			.bottom_menu_column_li_mail {
				a {
					color: $main-green; } } } } }

.skoda_footer {
	background: #000;
	color: #fff;
	a:hover {
		text-decoration: none;
		color: #fff; }
	a {
		color: #fff; }
	.skoda_footer_top_block {
		text-align: right;
		border-bottom: 1px solid #fff;
		padding-top: 15px;
		padding-bottom: 15px;
		.skoda_footer_society {
			font-size: 22px;
			margin-left: 20px; } }
	.skoda_footer_bottomp_block {
		padding-top: 15px;
		padding-bottom: 15px;
		font-size: 12px;
		a {
			padding: 0 15px; } } }

